.main-page-container {
  background-image: url("https://i.ibb.co/4ggfnnC/mainbg.png"),
    url("https://i.ibb.co/fCXNZFJ/mainbgdown.png");
  background-position: left top, right bottom;

  height: 100vh;
  background-repeat: no-repeat;
}

.bold {
  font-weight: 600;
}

.link {
  color: #ffb905 !important;
  font-weight: 600;
  cursor: pointer;
}

.top-menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 20px;
  width: 800px;
  float: right;
  margin-top: 30px;
  margin-right: 25px;
}
.content-container {
  width: 100%;
  float: right;
}

.meeting-info-box {
  float: right;
  margin-top: 100px;
  margin-right: 250px;
  width: 650px;
}
.meeting-info-title1 {
  font-family: "Fingerpaint";
  font-size: 60px;
}

.meeting-info-title2 {
  font-family: "Fingerpaint";
  font-size: 35px;
  margin-left: 30px;
}

.meeting-info-subtitle {
  font-family: "Garet";
  font-size: 30px;
  color: #ffb905;
  margin-left: 60px;
}

.meeting-info-subtitle2 {
  font-family: "Garet";
  font-size: 24px;
  color: black;
  font-weight: 600;
  margin-top: 30px;
}
.meeting-info-text {
  font-size: 20px;
  font-family: "OpenSansLight";
  width: 550px;
}

